import React, { useState, useEffect } from 'react';
import { ref, onValue } from 'firebase/database';
import { database } from '../firebaseConfig';
import { Chart } from 'react-chartjs-2'; // Assuming you use chart.js, install via npm if not
import '../index.css'; // Ensure this includes the necessary styles

const ClaimStatistics = () => {
  const [openCases, setOpenCases] = useState(0);
  const [closedCases, setClosedCases] = useState(0);
  const [lostClaims, setLostClaims] = useState(0);
  const [damagedClaims, setDamagedClaims] = useState(0);
  const [claimsOverTime, setClaimsOverTime] = useState([]);
  const [topCustomers, setTopCustomers] = useState([]);

  useEffect(() => {
    const claimsRef = ref(database, 'claims');
    onValue(claimsRef, (snapshot) => {
      const claimsData = snapshot.val();
      const open = Object.values(claimsData).filter(claim => claim.status === 'open').length;
      const closed = Object.values(claimsData).filter(claim => claim.status === 'closed').length;
      const lost = Object.values(claimsData).filter(claim => claim.claimType === 'lost').length;
      const damaged = Object.values(claimsData).filter(claim => claim.claimType === 'damaged').length;

      // Calculate claims over time and top customers
      const claimsByDate = {};
      const customerClaims = {};

      Object.values(claimsData).forEach(claim => {
        const date = claim.createdDate.split(' ')[0]; // Assuming 'YYYY-MM-DD' format
        claimsByDate[date] = (claimsByDate[date] || 0) + 1;

        if (customerClaims[claim.customerEmail]) {
          customerClaims[claim.customerEmail] += 1;
        } else {
          customerClaims[claim.customerEmail] = 1;
        }
      });

      setOpenCases(open);
      setClosedCases(closed);
      setLostClaims(lost);
      setDamagedClaims(damaged);
      setClaimsOverTime(Object.entries(claimsByDate));
      setTopCustomers(Object.entries(customerClaims).sort((a, b) => b[1] - a[1]));
    });
  }, []);

  // Data for the chart
  const data = {
    labels: claimsOverTime.map(([date]) => date),
    datasets: [{
      label: 'Claims',
      data: claimsOverTime.map(([, count]) => count),
      backgroundColor: 'rgba(54, 162, 235, 0.5)',
    }],
  };

  return (
    <div className="container mt-5">
      <h1>Claim Statistics</h1>
      <div className="statistics-overview">
        <div className="stat-container">
          <h2>Open Cases</h2>
          <p>{openCases}</p>
        </div>
        <div className="stat-container">
          <h2>Closed Cases</h2>
          <p>{closedCases}</p>
        </div>
        <div className="stat-container">
          <h2>Lost Claims</h2>
          <p>{lostClaims}</p>
        </div>
        <div className="stat-container">
          <h2>Damaged Claims</h2>
          <p>{damagedClaims}</p>
        </div>
      </div>
      <div className="chart-container mt-5">
        <h2>Claims Over the Last 30 Days</h2>
        <Chart type="line" data={data} />
      </div>
      <div className="top-customers mt-5">
        <h2>Top Customers by Number of Claims</h2>
        <ul>
          {topCustomers.slice(0, 10).map(([email, count]) => (
            <li key={email}>{email}: {count} claims</li>
          ))}
        </ul>
      </div>
    </div>
  );
};

export default ClaimStatistics;
