import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { ref, onValue, update } from 'firebase/database';
import { database } from '../firebaseConfig';
import { format, differenceInDays } from 'date-fns';

const FollowUpClaims = () => {
  const [followUpClaims, setFollowUpClaims] = useState([]);
  const navigate = useNavigate();

  useEffect(() => {
    const claimsRef = ref(database, 'claims');
    onValue(claimsRef, (snapshot) => {
      const claimsData = snapshot.val();
      const followUpList = [];
      for (let id in claimsData) {
        const claim = claimsData[id];
        const lastUpdate = claim.lastUpdate ? new Date(claim.lastUpdate) : new Date(claim.createdDate);
        if (differenceInDays(new Date(), lastUpdate) >= 5 || claim.followUpRequired) {
          followUpList.push({ id, ...claim });
        }
      }
      setFollowUpClaims(followUpList);
    });
  }, []);

  const handleViewClaim = (claimId) => {
    navigate(`/claim/${claimId}`);
  };

  const handleClearFollowUp = (claimId) => {
    const note = prompt('Please add a note for clearing the follow-up:');
    if (note) {
      const claimRef = ref(database, `claims/${claimId}`);
      const lastUpdate = format(new Date(), 'yyyy-MM-dd HH:mm:ss');
      const newNote = { text: note, timestamp: lastUpdate };
      const updates = {
        followUpRequired: false,
        lastUpdate,
        notes: [...(followUpClaims.find(claim => claim.id === claimId).notes || []), newNote]
      };
      update(claimRef, updates);
      setFollowUpClaims(followUpClaims.filter(claim => claim.id !== claimId));
    }
  };

  return (
    <ul className="list-group">
      {followUpClaims.map((claim) => (
        <li key={claim.id} className="list-group-item d-flex justify-content-between align-items-center">
          <span onClick={() => handleViewClaim(claim.id)} style={{ cursor: 'pointer', textDecoration: 'underline' }}>
            {claim.trackingNumber} - Follow Up Required
          </span>
          <button className="btn btn-secondary btn-sm" onClick={() => handleClearFollowUp(claim.id)}>
            Clear Follow-Up
          </button>
        </li>
      ))}
    </ul>
  );
};

export default FollowUpClaims;
