import React, { useState } from 'react';
import { ref, push } from 'firebase/database';
import { database } from '../firebaseConfig';
import { format } from 'date-fns';
import FollowUpClaims from './FollowUpClaims';
import '../index.css'; // Ensure styles are applied globally

const LogClaim = () => {
  const [claimData, setClaimData] = useState({
    trackingNumber: '',
    customerEmail: '',
    claimType: 'lost',
    createdDate: format(new Date(), 'yyyy-MM-dd'),
    followUpDate: format(new Date(new Date().setDate(new Date().getDate() + 5)), 'yyyy-MM-dd'),
    lastUpdate: format(new Date(), 'yyyy-MM-dd HH:mm:ss') // Initialize lastUpdate to current date
  });

  const handleChange = (e) => {
    const { name, value } = e.target;
    setClaimData((prevData) => ({
      ...prevData,
      [name]: value,
    }));
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    const claimsRef = ref(database, 'claims');
    push(claimsRef, claimData);
    setClaimData({
      trackingNumber: '',
      customerEmail: '',
      claimType: 'lost',
      createdDate: format(new Date(), 'yyyy-MM-dd'),
      followUpDate: format(new Date(new Date().setDate(new Date().getDate() + 5)), 'yyyy-MM-dd'),
      lastUpdate: format(new Date(), 'yyyy-MM-dd HH:mm:ss') // Reset lastUpdate to current date
    });
  };

  return (
    <div className="container mt-5">
      <h1>Log New Claim</h1>
      <form onSubmit={handleSubmit}>
        <div className="mb-3">
          <label className="form-label">Tracking Number:</label>
          <input
            type="text"
            className="form-control"
            name="trackingNumber"
            value={claimData.trackingNumber}
            onChange={handleChange}
            required
          />
        </div>
        <div className="mb-3">
          <label className="form-label">Customer Email:</label>
          <input
            type="email"
            className="form-control"
            name="customerEmail"
            value={claimData.customerEmail}
            onChange={handleChange}
            required
          />
        </div>
        <div className="mb-3">
          <label className="form-label">Claim Type:</label>
          <select
            name="claimType"
            className="form-select"
            value={claimData.claimType}
            onChange={handleChange}
          >
            <option value="lost">Lost Package</option>
            <option value="damaged">Damaged Package</option>
          </select>
        </div>
        <button type="submit" className="btn btn-primary">Log Claim</button>
      </form>
      <h2 className="mt-5">Claims Needing Follow-Up</h2>
      <FollowUpClaims />
    </div>
  );
};

export default LogClaim;
