import React, { useState, useEffect } from 'react';
import { useParams } from 'react-router-dom';
import { ref, onValue, update } from 'firebase/database';
import { database } from '../firebaseConfig';
import { format } from 'date-fns';
import '../index.css'; // Ensure styles are applied globally

const ClaimDetails = () => {
  const { claimId } = useParams();
  const [claim, setClaim] = useState(null);
  const [note, setNote] = useState('');
  const [editing, setEditing] = useState(false);
  const [originalClaimType, setOriginalClaimType] = useState('');

  useEffect(() => {
    const claimRef = ref(database, `claims/${claimId}`);
    onValue(claimRef, (snapshot) => {
      const claimData = snapshot.val();
      setClaim({ ...claimData, status: claimData.status || 'open' }); // Default to 'open' if status is undefined
      setOriginalClaimType(claimData.claimType);
    });
  }, [claimId]);

  const handleStatusChange = () => {
    const newStatus = claim.status === 'closed' ? 'open' : 'closed';
    const claimRef = ref(database, `claims/${claimId}`);
    const lastUpdate = format(new Date(), 'yyyy-MM-dd HH:mm:ss');
    const newNote = { text: `Status changed to ${newStatus}`, timestamp: lastUpdate };
    const updates = {
      status: newStatus,
      notes: claim.notes ? [...claim.notes, newNote] : [newNote],
      lastUpdate,
      followUpRequired: false // Reset follow-up requirement when status changes
    };
    update(claimRef, updates);
    setClaim({ ...claim, status: newStatus, notes: updates.notes, lastUpdate, followUpRequired: false });
  };

  const handleAddNote = () => {
    const claimRef = ref(database, `claims/${claimId}`);
    const followUpDate = format(new Date(new Date().setDate(new Date().getDate() + 5)), 'yyyy-MM-dd');
    const lastUpdate = format(new Date(), 'yyyy-MM-dd HH:mm:ss');
    const newNote = { text: note, timestamp: lastUpdate };
    const updates = {
      notes: claim.notes ? [...claim.notes, newNote] : [newNote],
      followUpDate,
      lastUpdate
    };
    update(claimRef, updates);
    setClaim({ ...claim, notes: updates.notes, followUpDate, lastUpdate });
    setNote('');
  };

  const handleMarkFollowUp = () => {
    const claimRef = ref(database, `claims/${claimId}`);
    const lastUpdate = format(new Date(), 'yyyy-MM-dd HH:mm:ss');
    const updates = {
      followUpRequired: true,
      lastUpdate
    };
    update(claimRef, updates);
    setClaim({ ...claim, followUpRequired: true, lastUpdate });
  };

  const handleEdit = () => {
    setEditing(!editing);
  };

  const handleSave = () => {
    const claimRef = ref(database, `claims/${claimId}`);
    const lastUpdate = format(new Date(), 'yyyy-MM-dd HH:mm:ss');
    const updates = { ...claim, lastUpdate };
    let newNotes = claim.notes ? [...claim.notes] : [];

    // Log the claim type change in the notes
    if (claim.claimType !== originalClaimType) {
      const newNote = { text: `Claim type changed from ${originalClaimType} to ${claim.claimType}`, timestamp: lastUpdate };
      newNotes.push(newNote);
      updates.notes = newNotes;
      setOriginalClaimType(claim.claimType); // Update the original claim type
    }

    update(claimRef, updates);
    setClaim({ ...claim, notes: newNotes, lastUpdate });
    setEditing(false);
  };

  const handleCopyToClipboard = (text) => {
    navigator.clipboard.writeText(text).then(() => {
      alert('Copied to clipboard');
    });
  };

  if (!claim) return <div>Loading...</div>;

  return (
    <div className="container mt-5">
      <h1>Claim Details</h1>
      {editing ? (
        <div>
          <div className="mb-3">
            <label className="form-label">Tracking Number:</label>
            <div className="d-flex align-items-center">
              <input
                type="text"
                className="form-control"
                value={claim.trackingNumber}
                onChange={(e) => setClaim({ ...claim, trackingNumber: e.target.value })}
              />
              <button className="btn btn-outline-secondary ms-2" onClick={() => handleCopyToClipboard(claim.trackingNumber)}>Copy</button>
            </div>
          </div>
          <div className="mb-3">
            <label className="form-label">Customer Email:</label>
            <div className="d-flex align-items-center">
              <input
                type="email"
                className="form-control"
                value={claim.customerEmail}
                onChange={(e) => setClaim({ ...claim, customerEmail: e.target.value })}
              />
              <button className="btn btn-outline-secondary ms-2" onClick={() => handleCopyToClipboard(claim.customerEmail)}>Copy</button>
            </div>
          </div>
          <div className="mb-3">
            <label className="form-label">Claim Type:</label>
            <select
              className="form-select"
              value={claim.claimType}
              onChange={(e) => setClaim({ ...claim, claimType: e.target.value })}
            >
              <option value="lost">Lost Package</option>
              <option value="damaged">Damaged Package</option>
            </select>
          </div>
          <button className="btn btn-success" onClick={handleSave}>Save</button>
          <button className="btn btn-secondary ms-2" onClick={handleEdit}>Cancel</button>
        </div>
      ) : (
        <div>
          <p>Tracking Number: {claim.trackingNumber} <button className="btn btn-outline-secondary btn-sm ms-2" onClick={() => handleCopyToClipboard(claim.trackingNumber)}>Copy</button></p>
          <p>Customer Email: {claim.customerEmail} <button className="btn btn-outline-secondary btn-sm ms-2" onClick={() => handleCopyToClipboard(claim.customerEmail)}>Copy</button></p>
          <p>Claim Type: {claim.claimType}</p>
          <p>Status: <span className={claim.status === 'closed' ? 'status-closed' : 'status-open'}>
            {claim.status.charAt(0).toUpperCase() + claim.status.slice(1)}
          </span></p>
          <button className="btn btn-primary" onClick={handleEdit}>Edit</button>
        </div>
      )}
      <div className="mt-3">
        <label className="form-label">Add Note:</label>
        <textarea
          className="form-control"
          value={note}
          onChange={(e) => setNote(e.target.value)}
        />
        <button className="btn btn-primary mt-2" onClick={handleAddNote}>Add Note</button>
      </div>
      <button className={`btn mt-2 ${claim.status === 'closed' ? 'btn-success' : 'btn-danger'}`} onClick={handleStatusChange}>
        {claim.status === 'closed' ? 'Reopen Case' : 'Close Case'}
      </button>
      <button className="btn btn-warning mt-2" onClick={handleMarkFollowUp}>Mark for Follow-Up</button>
      <ul className="list-group mt-3">
        {claim.notes && claim.notes.sort((a, b) => new Date(a.timestamp) - new Date(b.timestamp)).map((note, index) => (
          <li key={index} className="list-group-item d-flex justify-content-between align-items-center">
            <span>{note.text}</span>
            <span className="text-muted small">{note.timestamp}</span>
          </li>
        ))}
      </ul>
    </div>
  );
};

export default ClaimDetails;
