import React, { useState, useEffect } from 'react';
import { ref, onValue } from 'firebase/database';
import { database } from '../firebaseConfig';
import { useNavigate } from 'react-router-dom';
import Pagination from './Pagination';
import '../index.css'; // Ensure styles are applied globally

const Dashboard = () => {
  const [claims, setClaims] = useState([]);
  const [search, setSearch] = useState('');
  const [currentPage, setCurrentPage] = useState(1);
  const [claimsPerPage] = useState(20);
  const navigate = useNavigate();

  useEffect(() => {
    const claimsRef = ref(database, 'claims');
    onValue(claimsRef, (snapshot) => {
      const claimsData = snapshot.val();
      const claimsList = [];
      for (let id in claimsData) {
        const claim = claimsData[id];
        claimsList.push({ id, ...claim, status: claim.status || 'open' });
      }
      claimsList.sort((a, b) => new Date(b.createdDate) - new Date(a.createdDate));
      setClaims(claimsList);
    });
  }, []);

  const handleSearch = (e) => {
    setSearch(e.target.value.toLowerCase());
  };

  const handleView = (claimId) => {
    navigate(`/claim/${claimId}`);
  };

  const filteredClaims = claims.filter((claim) =>
    claim.trackingNumber.toLowerCase().includes(search)
  );

  const indexOfLastClaim = currentPage * claimsPerPage;
  const indexOfFirstClaim = indexOfLastClaim - claimsPerPage;
  const currentClaims = filteredClaims.slice(indexOfFirstClaim, indexOfLastClaim);

  const paginate = pageNumber => setCurrentPage(pageNumber);

  return (
    <div className="claims-dashboard-container">
      <h1>Claims Dashboard</h1>
      <div className="mb-3">
        <input
          type="text"
          className="form-control"
          placeholder="Search by Tracking Number"
          value={search}
          onChange={handleSearch}
        />
      </div>
      <table className="table">
        <thead>
          <tr>
            <th>Tracking Number</th>
            <th>Customer Email</th>
            <th>Claim Type</th>
            <th>Date Created</th>
            <th>Last Update</th>
            <th>Status</th>
            <th>Actions</th>
          </tr>
        </thead>
        <tbody>
          {currentClaims.map((claim) => (
            <tr key={claim.id} style={{ backgroundColor: claim.followUpRequired ? '#ffe6e6' : 'inherit' }}>
              <td>
                {claim.trackingNumber} {claim.followUpRequired && <span>⏱️</span>}
              </td>
              <td>{claim.customerEmail}</td>
              <td>{claim.claimType}</td>
              <td>{claim.createdDate}</td>
              <td>{claim.lastUpdate || 'N/A'}</td>
              <td>
                <span className={claim.status === 'closed' ? 'status-closed' : 'status-open'}>
                  {claim.status.charAt(0).toUpperCase() + claim.status.slice(1)}
                </span>
              </td>
              <td>
                <div className="action-buttons">
                  <button className="btn btn-primary" onClick={() => handleView(claim.id)}>View</button>
                  <button className="btn btn-secondary" onClick={() => handleView(claim.id)}>Edit</button>
                </div>
              </td>
            </tr>
          ))}
        </tbody>
      </table>
      <Pagination
        claimsPerPage={claimsPerPage}
        totalClaims={filteredClaims.length}
        paginate={paginate}
      />
    </div>
  );
};

export default Dashboard;
