import React from 'react';
import { Link } from 'react-router-dom';
import logo from '../logo_4x.svg'; // Make sure the path is correct
import './NavBar.css'; // Ensure this path is correct based on your structure

const NavBar = () => {
  return (
    <nav className="navbar">
      <img src={logo} alt="Logo" className="navbar-logo" />
      <ul>
        <li><Link to="/">Dashboard</Link></li>
        <li><Link to="/log-claim">Log Claim</Link></li>
        <li><Link to="/claim-statistics">Claim Statistics</Link></li>
      </ul>
    </nav>
  );
};

export default NavBar;
